'use client'

import { useState } from 'react'
import { useAccount, useContractRead, useContractWrite, useWaitForTransaction } from 'wagmi'
import YDDShowABI from '../contracts/YDDShow.json'
import { useStatus } from '@/context/StatusContext'
import { parseEther } from 'viem'

// ÂêàÁ∫¶Âú∞ÂùÄ
const CONTRACT_ADDRESS = '0x87a84EBa190912a9015a2e74056c5ceE28D807B0'

// ‰∏ªÈ°µÁªÑ‰ª∂
export default function Home() {
  // Áä∂ÊÄÅÁÆ°ÁêÜ
  const [age, setAge] = useState<string>('')
  const [currentAge, setCurrentAge] = useState<number | null>(null)
  const [isValidAge, setIsValidAge] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const { setStatus } = useStatus()

  // Ëé∑ÂèñÈí±ÂåÖË¥¶Êà∑‰ø°ÊÅØ
  const { address, isConnected } = useAccount()

  // ËØªÂèñÂêàÁ∫¶ÊñπÊ≥ï
  const { data: contractAge, refetch } = useContractRead({
    address: CONTRACT_ADDRESS,
    abi: YDDShowABI.abi,
    functionName: 'getAge',
    watch: true, // Ëá™Âä®ÁõëÂê¨ÂèòÂåñ
  })

  // ÂÜôÂÖ•ÂêàÁ∫¶ÊñπÊ≥ï
  const { write: writeContract, data: writeData } = useContractWrite({
    address: CONTRACT_ADDRESS,
    abi: YDDShowABI.abi,
    functionName: 'setAge',
    onError: (error) => {
      console.error('Contract write error:', error)
      if (error.message.includes('User rejected the request') || error.message.includes('User denied transaction signature')) {
        setError('*Áî®Êà∑ÂèñÊ∂àÊìç‰Ωú')
      } else {
        setError('*ËÆæÁΩÆÂπ¥ÈæÑÂ§±Ë¥•ÔºåËØ∑Á°Æ‰øùÊÇ®Â∑≤ËøûÊé•Âà∞ Sepolia ÊµãËØïÁΩëÂπ∂Êã•ÊúâË∂≥Â§üÁöÑÊµãËØïÂ∏Å')
      }
      setStatus('Â∞±Áª™')
    }
  })

  // Á≠âÂæÖ‰∫§ÊòìÂÆåÊàê
  const { isLoading: isWritePending, isSuccess } = useWaitForTransaction({
    hash: writeData?.hash,
    onSuccess: () => {
      // ‰∫§ÊòìÊàêÂäüÂêéÂà∑Êñ∞Âπ¥ÈæÑÊòæÁ§∫
      refetch()
      // Ê∏ÖÈô§ÈîôËØØ‰ø°ÊÅØ
      setError(null)
      // ËÆæÁΩÆÁä∂ÊÄÅ‰∏∫Â∞±Áª™
      setStatus('Â∞±Áª™')
    },
    onError: (error) => {
      setError('‰∫§ÊòìÂ§±Ë¥•ÔºåËØ∑ÈáçËØï')
      console.error('Transaction error:', error)
      // ËÆæÁΩÆÁä∂ÊÄÅ‰∏∫Â∞±Áª™
      setStatus('Â∞±Áª™')
    },
  })

  // ËÆæÁΩÆÂπ¥ÈæÑ
  const handleSetAge = async () => {
    setError(null)
    if (!age) {
      setError('*ËØ∑ËæìÂÖ•Âπ¥ÈæÑ')
      return
    }
    
    try {
      const ageNum = BigInt(age)
      if (ageNum <= 0n || ageNum >= 150n) {
        setError('*ËØ∑ËæìÂÖ•ÊôÆÈÄöÁ¢≥Âü∫‰∫∫Á±ªÁöÑÂπ¥ÈæÑ')
        return
      }

      if (!writeContract) {
        setError('*ÂêàÁ∫¶ÂÜôÂÖ•ÊñπÊ≥ïÊú™ÂàùÂßãÂåñÔºåËØ∑Á°Æ‰øùÊÇ®Â∑≤ËøûÊé•Âà∞ Sepolia ÊµãËØïÁΩë')
        return
      }

      setStatus('ËÆæÁΩÆ‰∏≠...')
      writeContract({
        args: [ageNum],
      })
      setAge('')
    } catch (error) {
      setError('*ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑÂπ¥ÈæÑÊï∞ÂÄº')
      return
    }
  }

  // Ëé∑ÂèñÂπ¥ÈæÑ
  const handleGetAge = async () => {
    setError(null)
    try {
      setStatus('Ëé∑Âèñ‰∏≠...')
      await refetch()
      const ageValue = contractAge ? Number(contractAge) : null
      if (ageValue === null || ageValue <= 0) {
        setIsValidAge(false)
        setCurrentAge(null)
      } else {
        setIsValidAge(true)
        setCurrentAge(ageValue)
      }
      setStatus('Â∞±Áª™')
    } catch (error) {
      console.error('Error getting age:', error)
      setIsValidAge(false)
      setCurrentAge(null)
      setError('Ëé∑ÂèñÂπ¥ÈæÑÂ§±Ë¥•ÔºåËØ∑ÈáçËØï')
      setStatus('Â∞±Áª™')
    }
  }

  return (
    <main className="container mx-auto p-4">
      <div className="max-w-2xl mx-auto">
        {/* Èí±ÂåÖ‰ø°ÊÅØ */}
        <div className="bg-gray-100 p-4 rounded-lg mb-6">
          <h2 className="text-xl font-bold mb-2">Èí±ÂåÖ‰ø°ÊÅØ</h2>
          <p>
            <span className="font-semibold">Áä∂ÊÄÅÔºö</span>
            {isConnected ? 'Â∑≤ËøûÊé•' : 'Êú™ËøûÊé•'}
          </p>
          {address && (
            <p>
              <span className="font-semibold">Âú∞ÂùÄÔºö</span> {address}
            </p>
          )}
        </div>

        {/* ÂêàÁ∫¶‰∫§‰∫í */}
        <div className="bg-white shadow-md rounded-lg p-6">
          <h2 className="text-2xl font-bold mb-4">ÂêàÁ∫¶‰∫§‰∫í</h2>
          
          {/* ÈîôËØØÊèêÁ§∫ */}
          {error && (
            <div className="mb-4 p-3 bg-red-100 text-red-700 rounded-lg">
              {error}
            </div>
          )}

          {/* ÊàêÂäüÊèêÁ§∫ */}
          {isSuccess && (
            <div className="mb-4 p-3 bg-green-100 text-green-700 rounded-lg">
              Êìç‰ΩúÊàêÂäüÔºÅ
            </div>
          )}
          
          {/* ËÆæÁΩÆÂπ¥ÈæÑ */}
          <div className="mb-6">
            <h3 className="text-lg font-semibold mb-2">ËÆæÁΩÆÂπ¥ÈæÑ</h3>
            <div className="flex gap-2">
              <input
                type="number"
                value={age}
                onChange={(e) => setAge(e.target.value)}
                placeholder="ËØ∑ËæìÂÖ•Âπ¥ÈæÑ"
                className="flex-1 p-2 border rounded"
                min="1"
                max="149"
              />
              <button
                onClick={handleSetAge}
                className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600 disabled:bg-gray-400"
                disabled={!isConnected || isWritePending}
              >
                {isWritePending ? 'Â§ÑÁêÜ‰∏≠...' : 'ËÆæÁΩÆ'}
              </button>
            </div>
          </div>

          {/* Ëé∑ÂèñÂπ¥ÈæÑ */}
          <div>
            <h3 className="text-lg font-semibold mb-2">Ëé∑ÂèñÂπ¥ÈæÑ</h3>
            <div className="flex gap-2 items-center">
              <button
                onClick={handleGetAge}
                className="bg-green-500 text-white px-4 py-2 rounded hover:bg-green-600 disabled:bg-gray-400"
                disabled={!isConnected}
              >
                Ëé∑Âèñ
              </button>
              {currentAge !== null && isValidAge ? (
                <span className="text-lg">ÂΩìÂâçÂπ¥ÈæÑÔºö{currentAge}</span>
              ) : (
                currentAge === null && !isValidAge && (
                  <span className="text-lg text-gray-500">sorryÔºåÊ≤°ÊúâÊâæÂà∞ËØ•Êï∞ÊçÆ= =ÔºåÂèØËÉΩÊòØËØ•ÂêàÁ∫¶ËøòÊ≤°ÊúâËÆæÁΩÆÂπ¥ÈæÑÔºåÊàñËÄÖÂá∫Èîô‰∫ÜÔºåËØ∑ËÅîÁ≥ªÂ∑•‰Ωú‰∫∫ÂëòÔºåËôΩÁÑ∂ËÅîÁ≥ª‰∫Ü‰πüÊ≤°Áî®üòÅ</span>
                )
              )}
            </div>
          </div>
        </div>
      </div>
    </main>
  )
}
